import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatTabGroup } from '@angular/material/tabs';
import { TranslateService } from '@ngx-translate/core';
import { RemoteService } from '@app/remote.service';
import { CookieService } from 'ngx-cookie-service';
import swal from 'sweetalert';
import { Router } from '@angular/router';
import { MatPaginator } from '@angular/material/paginator';
import { BgiJsonconfigServices } from '@app/config/BGIConfig/bgi-jsonconfig-services';
import moment from 'moment';
import { ActivatedRoute } from '@angular/router';
import { ApplicationService } from '@app/services/application.service';
@Component({
  selector: 'app-desktopreview',
  templateUrl: './desktopreview.component.html',
  styleUrls: ['./desktopreview.component.scss'],
  encapsulation: ViewEncapsulation.None,

})
export class DesktopreviewComponent implements OnInit {
  i18n(key) {
    return this.translate.instant(key);
  }
  paginationSet =
  BgiJsonconfigServices.bgiConfigData.configuration
    .enterpriseAdminPaginatonSet;

@ViewChild("paginator") paginator: MatPaginator;



traiprovname:any;
applirefno:any;
applytype:any;
courstype:any;
applstat:any;
certstat:any;
siteloca:any;
dateexoi:any;
addon:any;
lastupdat:any;
appdecon:any;
dateofexpiry:any;

active:boolean= false;
decline:boolean= false;
new:boolean = false;



  bronze: boolean = false;
  gold: boolean = false;
  @ViewChild('MatTabGroup') tabGroup: MatTabGroup;
  @ViewChild("mytab", { static: true }) mattab: any;
  @Input() approved;
disableSubmitButton: boolean = false
  constructor(private translate: TranslateService,
    private remoteService: RemoteService,
    private cookieService: CookieService,private route: Router,   private activatedRoute: ActivatedRoute , private applicationservice :ApplicationService) { }

    languagelist = [{ "id": "1", "languageName": "English", "languagecode": "en", "CountryMst_Pk": "136", "dir": "ltr" },
    { "id": "2", "languageName": "Arabic", "languagecode": "ar", "CountryMst_Pk": "31", "dir": "rtl" }]
    dir = 'ltr';
    ranges: any = {
      'Today': [moment(), moment()],
      'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
      'Last 7 Days': [moment().subtract(6, 'days'), moment()],
      'Last 30 Days': [moment().subtract(29, 'days'), moment()],
      'This Month': [moment().startOf('month'), moment().endOf('month')],
      'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
    }

  ngOnInit(): void {
    console.log(this.approved);

    this.activatedRoute.queryParams.subscribe((params) => {
      let app_ref_id = params['app_ref_id'];
    
      console.log(app_ref_id); 

      if(app_ref_id == '' || app_ref_id == undefined)
      {
        return
      }
      else{
       
        this.applicationservice.setValueStandaradCustomizeApproval(app_ref_id).subscribe(res =>
          {
    
           this.traiprovname = res.data[0].trainprovname;
           this.applirefno = res.data[0].applictionno;
           this.applytype = res.data[0].applytype;
           this.courstype = res.data[0].coursetype;
           this.applstat = res.data[0].applicationstatus;
           this.siteloca = res.data[0].sitelocan;
           this.addon = res.data[0].appdt_appdecon;
           this.lastupdat =res.data[0].appdt_updatedon;
          this.appdecon   =res.data[0].appdt_appdecon;
          this.dateofexpiry = res.data[0].dateofexpiry;
     
           const e = this.dateofexpiry;
            
         if(e == '' || e == null)
         {
          this.active= false;
          this.decline = false;
          this.new = true;
         }
         else if(e != '' || e != null)
         {
         
          this.new = false;
        
        
          const currdate = new Date();
          const expiry_date = new Date(e);
          if(currdate > expiry_date)
          {
            this.active= true;
            this.decline = false;
          } else if(currdate < expiry_date)
          {
            this.active= false;
            this.decline = true;
          } else 
          {
            this.active= true;
            this.decline = false;
        
          }
        
         }
           
        
        
           
          
          


           
          })
      }

    });

    
   

    
    if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
      const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
      //this.patientCategory.get('patientCategory').setValue(toSelect);
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
    } else {
      const toSelect = this.languagelist.find(c => c.id == '1');
      //this.patientCategory.get('patientCategory').setValue(toSelect);
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
    }
    this.remoteService.getLanguageCookie().subscribe(data => {
      this.translate.setDefaultLang(this.cookieService.get('languageCode'));
      if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
        const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
        //this.patientCategory.get('patientCategory').setValue(toSelect);
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
      } else {
        const toSelect = this.languagelist.find(c => c.id == '1');
        //this.patientCategory.get('patientCategory').setValue(toSelect);
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
      }
    });
  }
  next() {
    this.mattab = 1;
  }
  cancle() {
    swal({
      title: this.i18n('desktop.doyouwant'),
      text: '',
      icon: 'warning',
      buttons: [this.i18n('desktop.no'), this.i18n('desktop.yes')],
      dangerMode: true,
      closeOnClickOutside: false
    }).then((willGoBack) => {
      if (willGoBack) {
        this.disableSubmitButton = true;
       this.route.navigate(['/standardcourseapproval/approvaldetails']);
       setTimeout(() => {
        this.disableSubmitButton = false;
       }, 2000);
      }
    })
  }
  documentnext() {
    this.mattab = 3;
    console.log(0)
  }
  documentpre() {
    this.mattab = 1;
  }
  interprevious() {
    this.mattab = 0;
  }
  internext() {
    this.mattab = 2
  }




  

}
